<template>
	<div id="detail" v-if="movie">
		<div id="detail-pic">
			<a-card :bordered="false" style="width: 240px">
				<img slot="cover" alt="example" :src="picRequestPath + 'poster/' + movie.pic" />
			</a-card>
		</div>
		<div id="detail-text">
			<a-card :title="movie.name" :bordered="false" style="width: 700px" :headStyle="tstyle">
				<div id="card-text">
					<p>导演：<b>{{movie.directors}}</b></p>
					<p>编剧：<b>{{movie.writers}}</b></p>
					<p>演员：<b>{{movie.stars}}</b></p>
					<p>类别：<b>{{movie.genre}}</b></p>
					<p>片长：<b>{{movie.playTime}}</b></p>
					<p>上映日期：<b>{{movie.releaseTime}}</b></p>
				</div>
			</a-card>
		</div>
		<a-divider orientation="left">
			简介
		</a-divider>
		<div id="detail-intro">
			<a-card :bordered="false" style="width: 1200px">
				<div id="detail-intro-text">
					<p>{{movie.intro}}</p>
				</div>
			</a-card>
		</div>
		<a-divider orientation="left">
			影评
		</a-divider>
		<div v-if="$store.state.userInfo">
			<div class="detail-comment">
				<a-list class="comment-list" :header="`${comments.length} ${comments.length > 1 ? 'replies' : 'reply'}`"
				 item-layout="horizontal" :data-source="comments">
					<a-list-item slot="renderItem" slot-scope="item, index">
						<a-comment :author="item.uname" :avatar="picRequestPath + 'headpic/' + item.headPic">
							<p slot="content">{{ item.content }}</p>
							<a-tooltip slot="datetime" :title="moment(item.createdTime).format('YYYY-MM-DD HH:mm:ss')">
								<span>{{ moment(item.createdTime).fromNow() }}</span>
							</a-tooltip>
						</a-comment>
					</a-list-item>
				</a-list>
			</div>
			<a-divider orientation="left">
				说点什么吧...
			</a-divider>
			<div class="detail-comment">
				<a-comment>
					<a-avatar slot="avatar" :src="picRequestPath + 'headpic/' +userInfo.headPic" :alt="userInfo.name" />
					<div slot="content">
						<a-form-item>
							<a-textarea :rows="4" :value="value" @change="handleChange" style="width: 1100px;"/>
						</a-form-item>
						<a-form-item>
							<a-button html-type="submit" :loading="submitting" type="primary" @click="handleSubmit">
								提交
							</a-button>
						</a-form-item>
					</div>
				</a-comment>
			</div>
		</div>
		<div class="detail-comment" v-else>
			<p><a href="/">登录</a>后查看</p>
		</div>
	</div>
</template>

<script>
	import moment from 'moment';
	import {
		mapMutations,
		mapState
	} from "vuex";
	export default {
		async created() {
			this.getMovieDetail();
			this.getComments();
		},
		watch:{
			$route: async function(data) {
				this.getMovieDetail();
				this.getComments();
			}
		},
		computed: {
			...mapState(['requestPath', 'userInfo', 'movie', 'movieComments','picRequestPath'])
		},
		methods: {
			...mapMutations(["setMovie", "setMovieComments"]),
			async getMovieDetail() {
				let {
					data
				} = await this.$get(this.$store.state.requestPath + 'movie/' + this.$route.query.mid+ '/' + this.userInfo.uid);
				this.setMovie(data);
			},
			async getComments() {
				let {
					data
				} = await this.$get(this.$store.state.requestPath + 'comment/get/' + this.$route.query.mid);
				this.setMovieComments(data);
				this.comments = this.$store.state.movieComments;
			},
			addComment(content) {
				let comment = {
					uid: this.$store.state.userInfo.uid,
					mid: this.$route.query.mid,
					content: content
				};
				let {
					data
				} = this.axios.post(this.$store.state.requestPath + 'comment/add', comment).then((res) => {
					if (res.data.code != 0) {
						this.$message.error(' (￣△￣；) 提交评论失败~ 再试试吧 ', 1);
					} else {
						this.$message.success(' <(￣︶￣)> 感谢您的评论~ ', 1);
					}
				});
			},
			handleSubmit() {
				if (!this.value) {
					return;
				}
				this.submitting = true;
				setTimeout(() => {
					this.addComment(this.value);
					this.submitting = false;
					this.comments = [{
							uname: this.$store.state.userInfo.name,
							headPic: this.$store.state.userInfo.headPic,
							content: this.value,
							createdTime: moment(),
						},
						...this.comments,
					];
					this.value = '';
				}, 1000);
			},
			handleChange(e) {
				this.value = e.target.value;
			},
		},
		data() {
			return {
				tstyle: {
					"font-size": "xx-large"
				},
				comments: this.$store.state.movieComments,
				submitting: false,
				value: '',
				moment,
			};
		},
	}
</script>

<style lang='scss'>
	#detail {
		margin: 0 120px 50px 120px;

		#detail-pic {
			float: left;
			margin: 70px 20px 50px 150px;
		}

		#detail-text {
			float: left;
			padding: 30px;
			margin: 30px 20px 50px 40px;

			#card-text {
				font-size: medium;
				padding: 20px 30px 20px 24px;
				margin: -20px;
				background: #ECECEC;
			}
		}

		#detail-intro {
			float: left;
			margin: -10px 20px 30px 70px;

			#detail-intro-text {
				padding: 5px 30px 20px 30px;
				margin: -20px;
				background: #ECECEC;
				font-size: medium;
			}
		}

		.detail-comment {
			margin: 10px 20px 20px 70px;
		}
	}
</style>
